code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --theme-primary:#6610f2;
}
.wrapper-chart{
  display: grid;
  grid-row: 1/2;
  grid-column: 1/2;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 6%;
  grid-gap: 1% 1%;
  padding: 3% 3%;
  background: #fff;
  border-radius: 10px;
  min-width: 0;
  min-height: 0;
}

.wrapper {
  display: grid;
  width: 100vw;
  height: 100vh;
  padding: 10px;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-template-columns: 100%;
  grid-template-rows: 1fr 1fr;
  background: var(--theme-primary);
  min-height: 0;
  min-width: 0;
}
.chart1 {
  grid-column: 1/2;
  grid-row: 1/2;
}
.items-button{
  display: flex;
  flex-flow: row wrap;
  grid-column: 1/2;
  grid-row: 2/3;
  justify-content: flex-end;
  overflow: hidden;
} 
.sidebar-input{
  grid-area: "graph-input";
  min-width: 0;
  min-height: 0;

}
.sidebar-history{
  grid-area: "history";
  grid-column: 1/2;
  grid-row: 3/4;
  min-height: 0;
  min-width: 0;
  overflow: hidden;
}


@media screen and (min-width: 500px){
  .wrapper-chart{
    display: grid;
    grid-area: a;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 6%;
    grid-gap: 1% 1%;
    padding: 3% 3%;
    background: #fff;
    border-radius: 10px;
    min-width: 0;
    min-height: 0;
  }
  
  .items-button{
    display: flex;
    flex-flow: row-reverse wrap;
    grid-column: 3/4;
    grid-row: 4/5;
    justify-content: flex-end;
  } 
  .chart1 {
    grid-column: 1/4;
    grid-row: 1/4;
  }
  .wrapper {
    display: grid;
    width: 100vw;
    height: 100vh;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    grid-template-columns: 1em repeat(4,1fr) 1em;
    grid-template-rows: 2em repeat(2,1fr) 2em;
    background: var(--theme-primary);
    min-height: 0;
    min-width: 0;
    grid-template-areas:
    ". . . . . ."
    ". a a a a ."
    ". a a a a ."
    ". s s s s ."
    ". s s s s ."
    ". . . . . .";
  }
  .wrapper-sidebar{
    display: grid; 
    grid-area: s;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px 10px;
    min-height: 0;
    min-width: 0;
    box-shadow: 10px 14px 80px rgba(34, 35, 58, 0.2);
    background: #fff;
    border-radius: 10px;
    grid-template-areas:
    "graph-input history"
    "graph-input history"
  }

  .sidebar-input{
    grid-area: "graph-input";
    min-width: 0;
    min-height: 0;
  }
  
  .sidebar-input{
    grid-area: "graph-input";
    min-width: 0;
    min-height: 0;
  }
  
  .sidebar-history{
    grid-area: "history";
    grid-column: 2/3;
    grid-row: 1/2;
    min-height: 0;
    min-width: 0;
  }
  
}

@media screen and (min-width: 760px){
  .wrapper-chart{
    display: grid;
    grid-area: a;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 6%;
    grid-gap: 1% 1%;
    padding: 3% 3%;
    background: #fff;
    border-radius: 10px;
    min-width: 0;
    min-height: 0;
  }
  .wrapper {
    display: grid;
    width: 100vw;
    height: 100vh;
    /* grid-gap: 1% 1%; */
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    grid-template-columns: 1em repeat(3,1fr) minmax(300px, 1fr) 1em;
    grid-template-rows: 2em repeat(4,1fr) 2em;
    background: var(--theme-primary);
    min-height: 0;
    min-width: 0;
    grid-template-areas:
    ". . . . . ."
    ". a a a s ."
    ". a a a s ."
    ". a a a s ."
    ". a a a s ."
    ". . . . . .";
  }
  .wrapper-sidebar{
    display: grid; 
    grid-area: s;
    grid-template-columns: minmax(300px,1fr);
    grid-template-rows: 1fr 2fr;
    grid-gap: 10px 10px;
    min-height: 0;
    min-width: 0;
    box-shadow: 10px 14px 80px rgba(34, 35, 58, 0.2);
    background: #fff;
    border-radius: 10px;
    grid-template-areas:
    "graph-input"
    "history"
    "history";
  }
  .sidebar-input{
    grid-area: "graph-input";
  }
  .sidebar-input{
    grid-area: "graph-input";
    min-width: 0;
    min-height: 0;
  }


  .sidebar-history{
    grid-column: 1/2;
    grid-row: 2/3;
    grid-area: "history";
  }
  .items-button{
    display: flex;
    flex-flow: row-reverse wrap;
    grid-column: 3/4;
    grid-row: 4/5;
    justify-content: flex-end;
    min-width: 0;
    min-height: 0;
  } 
}

.graph{
    display: block;
    background-color: #fff;
    font-size: 12 !important;

}

.funcInput{
    flex-basis: 100%;
}
.form{
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    justify-content: space-between;
    background: #fff;
    border-radius: 10px;
}

.col1{
    flex-basis: 100%;
    min-width: 0;
    padding: 5px;
}

.container{
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    justify-content: flex-start;
    flex-wrap: nowrap;
    min-height: 0;
    min-width : 0;
    overflow: auto;
}

@media screen and (min-width: 760px){
    .container{
        display: flex;
        flex-direction: column;
        padding: 10px 10px;
        justify-content: flex-start;
        flex-wrap: nowrap;
        min-height: 0;
        min-width : 0;
    }
}



.container{
    display: flex;
    flex-direction: column;
    min-width: 0;
    min-height: 0;
    flex-flow: 1;
    background: #fff;
}





.card-layout{
    display: grid; 
    grid-template-columns: minmax(100px,1fr) 5px 49px;
    grid-template-rows: 25px 38px;
    padding: 2.5px;
    /* box-shadow: 10px 14px 80px rgba(34, 35, 58, 0.2); */
    background: #fff;
    grid-template-areas:
    "function1 button1 button2"
    "function2 function2 function2";
  }

.functionInput{
    grid-column: 1/4;
    grid-row: 2/3;
    background: #fff;
}
.button1{
    grid-area: "button1";
    justify-self: flex-start;
}
.button2{
    grid-column: 3/4;
    grid-row: 1/2;
    grid-area: "button2";
    justify-self: center;
    align-self: center;
    background: #fff;
}
.function-name{
    grid-column: 1/2;
    grid-row: 1/2;
    justify-self: flex-start;
}

#bodyId .ui.create-new-menu-btn {
    border-radius: 0;
    background-color: #fff;
}
